.BMIResultTypography {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  line-height: 0.2 !important;
}

.BMIResultTypography h3 {
  line-height: 0.9 !important;
}

.BMIResultTypography h6 {
  font-weight: 400 !important;
}

.BMI-rail-container {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  margin: 48px 0 12px;
}

.BMI-mark {
  position: absolute;
  width: 100%;
  text-align: center;
}

.BMI-mark-first {
  transform: translate(-25%, -100%);
}

.BMI-mark-second {
  transform: translate(0, -100%);
}

.BMI-mark-third {
  transform: translate(25%, -100%);
}

.BMI-label {
  font-size: 0.8rem !important;
  text-align: center;
}

.BMI-color-under {
  color: #4cd0df;
}

.BMI-color-normal {
  color: #4cdf51;
}

.BMI-color-over {
  color: #f1e046;
}

.BMI-color-obese {
  color: #f15d46;
}

.BMI-rail {
  width: 100%;
}

.BMI-rail-thumb {
  position: absolute;
  height: 270%;
}
