.calorie-expenditure-inforgraphy-container {
  position: relative;
  height: 200px;
}

.calorie-expenditure-meter-container {
  height: 100%;
}

.calorie-expenditure-meter {
  display: block;
  margin-left: auto;
  margin-right: auto;
  height: 100%;
}

.calorie-expenditure-labels-container {
  position: relative;
  padding-right: 8px !important;
}

.calorie-expenditure-meter-label {
  max-width: 100%;
  font-weight: 400 !important;
  text-align: center;
}

.calorie-expenditure-meter-calories {
  font-weight: 400 !important;
  margin-left: 16px !important;
}

.calorie-expenditure-meter-cal {
  font-size: 1rem !important;
}

.calorie-expenditure-color-under {
  color: #4cd0df !important;
}

.calorie-expenditure-color-normal {
  color: #4cdf51 !important;
}

.calorie-expenditure-color-over {
  color: #f15d46 !important;
}

.calorie-expenditure-top {
  position: absolute;
  transform: translateY(-66px);
}

.calorie-expenditure-middle {
  position: absolute;
}

.calorie-expenditure-bottom {
  position: absolute;
  transform: translateY(66px);
}
