.weight-range-infographic-container {
  position: relative;
  width: 100%;
  margin-top: 60px;
}

.weight-range-mark {
  position: absolute;
  width: 100%;
  text-align: center;
  line-height: 0.8 !important;
}

.weight-range-min {
  color: #4cd0df;
  transform: translate(-23%, -90%);
}

.weight-range-max {
  color: #f15d46;
  transform: translate(23%, -90%);
}

.weight-range-rail-container {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 8px;
}

.weight-range-rail {
  width: 100%;
}

.weight-range-rail-thumb-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: absolute;
  height: 100%;
}

.weight-range-rail-thumb {
  height: 45%;
}

.weight-range-rail-thumb-label {
  position: absolute;
  transform: translateY(90%);
}

.weight-range-rail-thumb-label-normal {
  color: #4cdf51;
}

.weight-range-rail-thumb-label-under {
  color: #4cd0df;
}

.weight-range-rail-thumb-label-over {
  color: #f15d46;
}
