.ResultsPanel {
  position: absolute;
  margin-left: auto;
  margin-right: auto;
  padding-bottom: 8px;
  top: 20vh;
  left: 0;
  right: 0;
  background-color: white;
  width: 100%;
}
.ResultsPanel-container {
  padding: 0 !important;
}
.results-panel-back-button {
  margin: 16px !important;
  align-self: flex-start;
}

.results-panel-action-button {
  margin: 4px 0 !important;
  height: 56px;
  color: white !important;
}

.panel-in-enter {
  transform: translateX(100vw);
}

.panel-in-enter-active {
  transform: translateX(0vw);
  transition: 600ms;
}

.panel-in-enter-appear {
  transform: translateX(0vw);
}

.panel-in-exit {
  transform: translateX(0vw);
}

.panel-in-exit-active {
  transform: translateX(100vw);
  transition: 600ms;
}
