.weight-typography {
  width: 70% !important;
  position: absolute;
  margin: auto !important;
  left: 0;
  right: 0;
  top: 12%;
}

.weight-result-infographic-container {
  position: relative;
  color: whitesmoke;
}

.weight-result-typography {
  text-align: center;
  transform: translateY(-1rem);
}

.weight-result-typography-normal {
  color: #4cdf51;
}
.weight-result-typography-under {
  color: #4cd0df;
}

.weight-result-typography-over {
  color: #f15d46;
}
